const form = document.getElementById("new-product-form");

console.log(form);

import { PhysicalProduct } from "./models/PhysicalProduct";
import { DigitalProduct } from "./models/DigitalProduct";
import { Product } from "./models/Product";
import { calculateTax } from "./utils/taxCalcuator";
import { ProductSorter } from "./utils/sorter";
import { DiscountableProduct } from "./interfaces/DiscountableProduct";

const device: PhysicalProduct = new PhysicalProduct(
  "DEV001",
  "Computer",
  1200,
  2.5
);
const software: DigitalProduct = new DigitalProduct(
  "VSC001",
  "VS Code",
  24,
  12.3
);
const ball: PhysicalProduct = new PhysicalProduct(
  "BAL001",
  "Basketball",
  5,
  4.5
);
const keyboard: PhysicalProduct = new PhysicalProduct(
  "KB001",
  "Mechanical Keyboard",
  150,
  1.2
);

let products: Product[] = [device, software, ball];

console.log("Product List and Pricing");
products.forEach((product) => {
  console.log(product.displayDetails());
  console.log(` Price (with tax): $${calculateTax(product).toFixed(2)}`);
});
console.log("------------------------");

console.log("--- Applying Discount ---");
if ((device as any as DiscountableProduct).applyDiscount) {
  console.log(`Original Device Price: $${device.price.toFixed(2)}`);
  const newDevicePrice = (device as any as DiscountableProduct).applyDiscount(
    0.1
  );
  console.log(`Device Price After 10% Discount: $${newDevicePrice.toFixed(2)}`);
  console.log(
    `Device Price With Tax (after discount): $${calculateTax(device).toFixed(
      2
    )}`
  );
}
console.log("\n-----------------------------------\n");

console.log("--- Applying Bulk Discount to ball (5 units) ---");
const bulkBall = new PhysicalProduct("BAL001", "Basketball", 5, 4.5);
const quantityToPurchase = 5;
const minQuantityForBulkDiscount = 3;
const bulkDiscountRate = 0.2;
console.log(`Original Ball Price: $${bulkBall.price.toFixed(2)}`);
console.log(`Purchasing ${quantityToPurchase} units.`);
bulkBall.applyBulkDiscount(
  quantityToPurchase,
  minQuantityForBulkDiscount,
  bulkDiscountRate
);
console.log(`Ball (after bulk discount): $${bulkBall.price.toFixed(2)}`);
console.log(
  `Ball Price With Tax (after bulk discount): $${calculateTax(bulkBall).toFixed(
    2
  )}`
);

console.log(
  "\n--- Applying Bulk Discount to Keyboard (2 units - does not qualify) ---"
);
const bulkKeyboard = new PhysicalProduct(
  "KB001",
  "Mechanical Keyboard",
  150,
  1.2
);
const quantityToPurchaseKB = 2;
const minQuantityForBulkDiscountKB = 5;
const bulkDiscountRateKB = 0.15;
console.log(`Original Keyboard Price: $${bulkKeyboard.price.toFixed(2)}`);
console.log(`Purchasing ${quantityToPurchaseKB} units.`);
bulkKeyboard.applyBulkDiscount(
  quantityToPurchaseKB,
  minQuantityForBulkDiscountKB,
  bulkDiscountRateKB
);
console.log(
  `Keyboard Price (after attempted bulk discount): $${bulkKeyboard.price.toFixed(
    2
  )}`
);
console.log(
  `Keyboard Price With Tax (after attempted bulk discount): $${calculateTax(
    bulkKeyboard
  ).toFixed(2)}`
);

console.log("\n-----------------------------------\n");

console.log("--- Products Sorted by Name (Ascending) ---");
const productsSortedByNameAsc = ProductSorter.sortByName(products, "asc");
productsSortedByNameAsc.forEach((p) => console.log(p.displayDetails()));

console.log("\n--- Products Sorted by Name (Descending) ---");
const productsSortedByNameDesc = ProductSorter.sortByName(products, "desc");
productsSortedByNameDesc.forEach((p) => console.log(p.displayDetails()));

console.log("\n--- Products Sorted by Price (Ascending) ---");
const productsSortedByPriceAsc = ProductSorter.sortByPrice(products, "asc");
productsSortedByPriceAsc.forEach((p) => console.log(p.displayDetails()));

console.log("\n--- Products Sorted by Price (Descending) ---");
const productsSortedByPriceDesc = ProductSorter.sortByPrice(products, "desc");
productsSortedByPriceDesc.forEach((p) => console.log(p.displayDetails()));

console.log("\n-----------------------------------\n");
