import { Product } from "./Product";
import { DiscountableProduct } from "../interfaces/DiscountableProduct";

export class PhysicalProduct extends Product implements DiscountableProduct {
  weight: number;

  constructor(sku: string, name: string, price: number, weight: number) {
    super(sku, name, price);
    this.weight = weight;
  }

  applyDiscount(percentage: number): number {
    if (percentage < 0 || percentage > 1) {
      console.warn("Discount percentage must be between 0 and 1.");
      return this.price;
    }
    this.price = this.price * (1 - percentage);
    return this.price;
  }

  /**
   * @param quantity
   * @param minQuantityForDiscount
   * @param discountPercentage
   * @returns
   */

  applyBulkDiscount(
    quantity: number,
    minQuantityForDiscount: number,
    discountPercentage: number
  ): number {
    if (quantity >= minQuantityForDiscount) {
      return this.applyDiscount(discountPercentage);
    }
    return this.price;
  }

  getPriceWithTax(): number {
    const taxRate = 0.1;
    return this.price * (1 + taxRate);
  }

  get formattedWeight(): string {
    return `${this.weight.toFixed(2)} kg`;
  }

  displayDetails(): string {
    return `${super.displayDetails}, Weight: ${this.formattedWeight};`;
  }
}
